import React from 'react'

import {
  App
} from '@personal-website/app'

import {
  Layout
} from '../core/Layout'

import Content from '../Content.json'

const dataJson = JSON.parse(JSON.stringify(Content))
export default class Index extends React.Component {
  constructor() {
    super()

    this.state = {
      route: 'home',
      routes: [],
      prefix: ``,
      website: ``
    }
  }

  componentDidMount(){
    const routes = window && window.location && window.location.pathname && window.location.pathname.split('/').slice(0)

    // tslint:disable:no-console
    console.warn('website - routes', routes)

    const prefix = routes[0]
    // const route = routes.length === 2 ? routes[1] : `${routes[1]}${ routes[2] ? `-${routes[2]}` : ''}${ routes[3] ? `-${routes[3]}` : 'home'}`
    const route = { name: '' }

    if ((routes.length === 1 || routes.length === 2) && !routes[1]) {
      Object.assign(route, {
        name: 'home'
      })
    }

    if ((routes.length === 2 || routes.length === 3) && routes[1] && !routes[2]) {
      Object.assign(route, {
        name: `${routes[1]}`
      })
    }

    if ((routes.length === 3 || routes.length === 4) && routes[1] && routes[2] && !routes[3]) {
      Object.assign(route, {
        name: `${routes[1]}-${routes[2]}`
      })
    }

    if ((routes.length === 4 || routes.length === 5) && routes[1] && routes[2] && routes[3] && !routes[4]) {
      Object.assign(route, {
        name: `${routes[1]}-${routes[2]}-${routes[3]}`
      })
    }

    const currentRoute = dataJson && dataJson.routes && dataJson.routes.filter((item) => item.name === route.name)[0]

    const {
      website
    } = dataJson.global

    const title = `${website.name}${website.split}${currentRoute.title}`

    this.setState({
      route: route.name,
      routes: routes.slice(1),
      prefix: `/${prefix}`,
      website: `${prefix}`,
      title
    })
  }

  render() {
    const {
      state
    } = this

    const {
      route,
      title,
      routes,
      website
    } = state

    return (route && <Layout title={title}>
      <App website={website} name={route} data={dataJson} routes={routes} />
    </Layout>)
  }
}
