import React, {
  ReactElement
} from 'react'
import { Helmet } from 'react-helmet'

import favicon from '../../assets/images/icons/favicon.ico'

interface LayoutInterface {
  title: string
  children: string | ReactElement
}

export const Layout: React.FunctionComponent<LayoutInterface> = (props): ReactElement => {
  const {
    title,
    children
  } = props

  return <>
    <Helmet title={ title }>
      <link rel="icon" href={ favicon } />
    </Helmet>

    { children }
  </>
}
